import axios from "axios";
/**
 * 
 */
let source;

//
export function setCancelSource() {
     source = axios.CancelToken.source(); // source
    return source.token; // source
}

//
export function stopCancelSource() {
    if (typeof source === "object") {
        source.cancel("stopQuest"); //
    }
}



