import CryptoJS from "crypto-js";

// 
const aesKey = "U2FsdGVkX19oil2jSpI2nhJcXe6OOnZk57R72HI="; //3216
//3216IV
const aesIv ="123456654321123456";

/**
 * 
 * @param {*} data
 * @param {*} key
 * @param {*} iv
 */
function getEnAesString(data, key, iv) {
     key  = CryptoJS.enc.Utf8.parse(key);
     iv   = CryptoJS.enc.Utf8.parse(iv);
    var encrypted =CryptoJS.AES.encrypt(data,key,
        {
            iv:iv,
            mode:CryptoJS.mode.CBC,
            padding:CryptoJS.pad.Pkcs7
        });
    return encrypted.toString();    //base64
}

/**
 * 
 * @param {*} data
 * @param {*} key
 * @param {*} iv
 */
function getDeAesString(data, key, iv) {
     key  = CryptoJS.enc.Utf8.parse(key);
     iv   = CryptoJS.enc.Utf8.parse(iv);
    var decrypted =CryptoJS.AES.decrypt(data,key,
        {
            iv:iv,
            mode:CryptoJS.mode.CBC,
            padding:CryptoJS.pad.Pkcs7
        });
    return decrypted.toString(CryptoJS.enc.Utf8);
}

export function encryption(data) {
    //
    var encrypted = getEnAesString(data.toString(), aesKey, aesIv); //
    return encrypted;
}

export function decryption(data) {
    //
    var decryptedStr = getDeAesString(data.toString(), aesKey, aesIv);
    return decryptedStr;
}



